<template>
 <div>
 <div class = "loginFrame">
  <el-row type="flex" justify="center">
        <el-form ref="loginForm" :model="logInfo" :rules="rules" status-icon label-width="80px">
            <el-form-item label="用户名" prop="name">
                <el-input v-model="logInfo.username"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="pass">
                <el-input v-model="logInfo.password" type="password"></el-input>
            </el-form-item>
            <el-form-item>
                <el-radio v-model="logInfo.type" label=1>医生</el-radio>
                <el-radio v-model="logInfo.type" label=2>病人</el-radio>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" icon="el-icon-upload" @click="login()">登录</el-button>
            </el-form-item>
        </el-form>
    </el-row>
    </div>
  </div>
</template>

<script>
export default {
  name:'login',
    data() {
      return {
          logInfo:{
              type:'1',
              username:'',
              password:''
          },
          rules: {
                    name: [
                        {required: true, message: '用户名不能为空', trigger: 'blur'}
                    ],
                    pass: [
                        {required: true, message: '密码不能为空', trigger: 'blur'}
                    ]
                }
      }
    },
    methods: {
      onSubmit() {
        console.log('submit!');
      },
      login()
      {
            this.$axios.post('/login', {
            type: this.logInfo.type,
            username: this.logInfo.username,
            password: this.logInfo.password
        })
        .then(response=>{
            console.log(response);
            if(response.data.code==200)
            {
              this.$router.push({
                path:'/index',
                query:{name:this.logInfo.username}
                });
            }
            else
            {
              alert("登录失败");
            }
        })
        .catch(function (error) {
            console.log(error);
        });          
     },
    }
}
</script>

<style>

</style>



